html {
  height: 100%;
  width: 100%;
}

body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', 
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  justify-content: center;
}

h2 {
  margin-bottom: 0;
  font-size: 32px;
  color: #00314e;
}

h4 {
  margin: 1rem 0 0.5rem 0;
  color: #00314e;
}

button {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', 
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 16px 12px;
  grid-gap: 12px;
  gap: 12px;

  position: relative;
  width: auto;
  height: 48px;

  background: #00879e;
  border: 1px solid #00879e;
  border-radius: 4px;

  color: #FFFFFF;
  font-size: 18px;
  font-weight: 500;
  
  transition: box-shadow .2s;
}

button:hover {
  box-shadow: 0px 6px 15px rgba(60, 216, 255, 0.4);
}

button:disabled {
  opacity: 0.6;
}

button:disabled:hover {
  box-shadow: none;
}

#root {
  height: 100%;
  width: 100%;
}

/* 
   Responsive web design for mobile device, 
   triggered when device's width is less than 600px.
*/
@media (max-width: 600px) {
  body{
    font-size: 14px;
  }

  h2 {
    margin: 0;
  }
}
.bg {
  width: 100%;
  height: 100%;
  background-image: url(/website-demo/static/media/2273-modified3.f2434ae2.png);
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  justify-content: center;
}

.mainRegion {
  display: flex;
  padding: 24px;

  position: absolute;
  width: 625px;
  height: 825px;
  top: 16px;

  background-color: rgba(255, 255, 255, 0.5);

  box-shadow: 0px 5px 10px 5px rgba(157, 185, 217, 0.2);
  border-radius: 4px;
}

.flexContainer {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: space-between;
  grid-gap: 1rem;
  gap: 1rem;
}

.snsBtnContainer {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  grid-gap: 2rem;
  gap: 2rem;
  justify-content: center;
}

.registerForm {
  position: absolute;
  top: 48px;
  left: 105px;
  right: 105px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  grid-gap: 1.5em;
  gap: 1.5em;
  box-sizing: border-box;
}

.snsBtn {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue';
  font-size: 16px;

  box-sizing: border-box;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;

  position: relative;
  width: 3rem;
  height: 3rem;

  background: #FFFFFF;
  border: 2px solid #00879e;
  border-radius: 50%;
  color: #00879e;
}

.snsBtn:hover {
  background: #00879e;
  color: #FFFFFF;
}

.snsBtn:active {
  background: #004b58;
  border: 2px solid #004b58;
  color: #FFFFFF;
}

.inputTextArea {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue';

  display: flex;
  flex-direction: row;
  padding: 16px 1rem;
  box-sizing: border-box;
  margin: 0.5rem 0 0.5rem 0;

  position: relative;
  width: 100%;
  height: auto;

  background: #ffffff;
  border-radius: 8px;
  border: 2px solid #00879e;

  font-weight: 500;
  font-size: 16px;
}

.inputTextArea::-webkit-input-placeholder {
  font-weight: normal;
}

.inputTextArea::placeholder {
  font-weight: normal;
}

.inputPasswordArea {
  display: flex;
  position: relative;
  align-items: center;
}

.miniPlaceholder {
  position: absolute;
  margin: 0.7rem 12px;
  font-size: 8px;
  top: 0px;
}

.returnBtn:hover {
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
  -webkit-text-decoration-color: #00879e;
          text-decoration-color: #00879e;
  text-decoration-thickness: 2px;
  font-weight: bold;
}

.checkBoxContainer {
  margin-bottom: 1rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  grid-gap: 0.8rem;
  gap: 0.8rem;
}

/* 
   Responsive web design for mobile device, 
   triggered when device's width is less than 600px.
*/
@media (max-width: 600px) {
  .bg {
    background-size: contain;
    background-position: left bottom;
  }

  .mainRegion {
    padding: 1.5rem 1rem;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    box-sizing: border-box;
    box-shadow: none;
  }

  .registerForm {
    left: 0;
    right: 0;
    margin: 1rem;
  }
}

@media (max-width: 1024px) and (min-width: 601px) {
  .bg {
    background-position: center;
  }
}
